import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../components/HelloFromVux.vue'
import TopicList from '../components/TopicList.vue';
import Topic from '../components/Topic.vue'
import Login from '@/components/Login.vue'
import Logout from '@/components/Logout.vue'
import Center from '@/components/Center.vue'
import Msg from '@/components/Msg.vue'
import User from '@/components/User.vue'
import CreateTopic from '@/components/CreateTopic'


Vue.use(VueRouter)

const routes = [
      // {
      // path: '/',
      // component: Home
      // },
      {
        path:'/',
        redirect: '/topiclist/all'
      },
      {
        path: '/topiclist/:name',
        name:"topiclist",
        component: TopicList,
        props:true
      },
      {
        path:'/topic/:id',
        name:'topic',
        component:Topic,
        props:true
      },
      //登录
      {
        path:'/login',
        name:'login',
        component:Login,
        props:true
      },
      //个人中心
      {
        path:'/center',
        name:'center',
        component:Center,
        props:true
      },
      //个人中心
      {
        path:'/user/:loginname',
        name:'user',
        component:User,
        props:true
      },
      //发表话题
      {
        path:'/createTopic',
        name:'createTopic',
        component:CreateTopic,
        props:true
      },
      //消息通知
      {
        path:'/msg',
        name:'msg',
        component:Msg,
        props:true
      },
      //退出登录
      {
        path:'/logout',
        name:'logout',
        component:Logout,
        props:true
      },
];

const router = new VueRouter({
  routes
});
export default router;


