
import Group from 'vux/src/components/group/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import WechatEmotion from 'vux/src/components/wechat-emotion/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import {mapState} from 'vuex'
export default {
name:'topic',
props:['id'],
data:function(){
return {
topic:null,
replyContent:'',
isShowPouup:false,
reply_id:'',
reply_loginname:''
}
},
components: {
Group,
CellBox,
Flexbox,
FlexboxItem,
WechatEmotion,
XTextarea,
XButton,
Popup
},
directives:{
TransferDom
},
computed:{
...mapState(['isLogined'])
},
methods: {
upOrDown:function(reply_id){
var app=this;
this.$http.post('/reply/'+reply_id+'/ups',{
accesstoken:this.$store.state.accesstoken
}).then(function(res){
console.log(res.data);
});
},
getDescByType(tab){
return this.$store.getters.getTopicDescByType(tab);
},
showPopup(reply_id,reply_loginname){
this.isShowPouup=true;
this.reply_id=reply_id;
this.reply_loginname=reply_loginname;
this.replyContent='@'+reply_loginname+' ';
},
replyTopic:function(replyObjType){
var app=this;
var params={
accesstoken:this.$store.state.accesstoken,
content:this.replyContent
};
if(replyObjType==2){
params.reply_id=this.reply_id;

}
this.$http.post('/topic/'+this.topic.id+'/replies',params)
.then(function(res){
var reply={
"id":res.data.reply_id,
"author":{
"loginname":app.$store.state.loginName,
"avatar_url":app.topic.avatar_url
},
"content":params.content,
"ups":[],
"create_at":'212',
"reply_id":null,
"is_uped":false
}
if(replyObjType==2){
reply.reply_id=app.reply_id;
}
app.topic.replies.push(reply);
app.replyContent='';
app.reply_id='';
app.reply_loginname='';
app.isShowPouup=false;
});

}
},
beforeRouteEnter (to, from, next) {
next(function(vm){
vm.$http.get('/topic/'+to.params.id).then(function(res){
if(res.data.success){
vm.topic=res.data.data;
}
});
});
},
beforeRputeUpdate(to,from,next){
var app=this;
this.$http.get('/topic/'+this.id).then(function(res){
if(res.data.success){
app.topic=res.data.data;
}
});
}
}



