
import Card from 'vux/src/components/card/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

export default {
name:'user',
props:['loginname'],
components: {
Card,
Group,
Cell
},
data:function(){
return {
userinfor:''
}
},
beforeRouteEnter (to, from, next) {
next(function(vm){
vm.$http.get('/user/'+to.params.loginname).then(function(res){
vm.userinfor=res.data.data;
});
});
},
beforeRoutrUpdate:function(to, from, next){
var app=this;
this.$http.get('/user/'+to.params.loginname).then(function(res){
app.userinfor=res.data.data;
});
next();
}
}



