
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import {mapState} from 'vuex';
export default {
name:'topiclist',
props:['name'],
components: {
Group,
Cell,
Scroller,
Tab,
TabItem
},
computed:{
...mapState(['topics',])
},
methods: {
getDescByType(tab){
return this.$store.getters.getTopicDescByType(tab);
},
pullUpMoreData:function(){
var app=this;
this.page=this.page+1;
this.$http.get('/topics',{
params:{
tab:this.name,
page:this.page,
limit:this.limit
}
}).then(function(res){
if(res.data.success){
for(var i=0;i<res.data.data.length;i++){
app.lists.push(res.data.data[i]);
}
app.$refs.pullUp.donePullup();
}
});
},
routeTo(index){
this.$router.push('/topiclist/'+this.topics[index].name);
}
},
data:function(){
return{
lists:[],

page:1,

limit:15,

pullupConfig:{
content: '上拉加载更多',
downContent: '松开进行加载',
upContent: '上拉加载更多',
loadingContent: '加载中...'
}
}
},
beforeRouteEnter (to, from, next) {
next(function(vm){
vm.$http.get('/topics',{
params:{
tab:to.params.name,
page:vm.page,
limit:vm.limit
}
}).then(function(res){
console.log(res);

if(res.data.success){
vm.lists=res.data.data;
}

})

});
},
beforeRouteUpdate (to,from ,next){
var app=this;
this.$http.get('/topics',{
params:{
tab:to.params.name,
page:this.page,
limit:this.limit
}
}).then(function(res){
if(res.data.success){
app.lists=res.data.data;
}
});
next();
}
}



