import {mapMutations} from 'vuex';
export default {
    name:'logout',
    created:function(){
        this.mutationLogout();
        window.localStorage.clear();
        this.$router.push('/topiclist/all');
    },
    methods: {
        ...mapMutations(['mutationLogout'])
    },
}



